@import 'scss/variables/color';

.wilco-square {
  width: 100px;
  height: 100px;
  margin-bottom: 25px;

  .content {
    color: #fff;
    position: relative;
    width: 100%;

    .color-name-container {
      left: 0;
      position: absolute;
      text-align: center;
      top: 63px;
      width: 100%;
    }
  }

  &--black {
    background-color: $pd-color-black;
  }

  &--blue {
    background-color: #327cc0;
  }

  &--brown {
    background-color: #a46c43;
  }

  &--green {
    background-color: #03895a;
  }

  &--light-blue {
    background-color: #95d4f0;
  }

  &--light-green {
    background-color: #64bb45;
  }

  &--maroon {
    background-color: #583035;
  }

  &--navy {
    background-color: #302a72;
  }

  &--orange {
    background-color: #ec6e31;
  }

  &--red {
    background-color: #aa1d26;
  }

  &--pink {
    background-color: #e45e97;
  }

  &--purple {
    background-color: #6c58a4;
  }

  &--white {
    background-color: #fff;
    border: solid 1px $pd-color-dark-gray;

    .font-color {
      color: #000;
    }
  }

  &--yellow {
    background-color: #efd515;
  }
}
