@import 'scss/variables/index.scss';

.pd-header {
  padding: $spacing-lg 0;
  position: absolute !important;
  width: 100%;
  z-index: 1;

  .hamburger-menu {
    color: $primary-color;
    cursor: pointer;

    i {
      font-size: $pd-font-size-md;
    }
  }

  &-logo {
    width: 100px;
  }

  &-menu {
    display: flex;
    justify-content: flex-end;

    &-item {
      color: $primary-color;
      padding: 0 $spacing-md;
      white-space: nowrap;

      &:hover {
        transition: color 0.2s;
        color: $pd-color-gray;
      }
    }
  }

  &-menu--mobile {
    background-color: rgba(255, 255, 255, 0.95);
    display: flex;
    flex-direction: column;
    height: 100%;
    left: 0%;
    position: fixed;
    top: 0%;
    width: 100%;

    .close-btn {
      cursor: pointer;
      position: fixed;
      z-index: 9;
    }

    .header {
      color: $primary-color;
      font-size: $pd-font-size-md;
      padding: $spacing-md $spacing-md;
    }

    .item {
      color: $primary-color;
      display: flex;
      flex-direction: column;
      font-size: $pd-font-size-sm;
      margin: 0 $spacing-lg;
      padding: $spacing-md 0;
      white-space: nowrap;

      &:not(:last-child) {
        border-bottom: solid 1px $primary-color;
      }
    }
  }
}
