@import 'scss/variables/screen-size';

@media only screen and (max-width: $small-desktop) {
  .pd-faq-extras {
    height: 312px;
  }
  .slick-slider{
    height: 312px;
  }
  .slick-list {
    height: 312px;
  }
}

@media only screen and (max-width: $tablet) {
  .pd-faq-extras {
    height: 512px;
  }
  .slick-slider{
    height: 512px;
  }
  .slick-list {
    height: 512px;
  }
  .ant-carousel .slick-initialized .slick-slide {
    height: 512px !important;
  }
} 

@media only screen and (max-width: $phone) {
  .pd-faq-extras {
    height: 250px;
  }
  .slick-slider{
    height: 250px;
  }
  .slick-list {
    height: 250px;
  }
}